<template lang="html">
  <div class="img-block">
    <div class="img-left">
      <div class="hd">{{title}}</div>
      <div class="msg">{{message}}</div>
    </div>
    <div class="img-right">
      <label :for="sign">
        <img :src="image || defaultimg" alt="">
        <input @change="changeFile" type="file" accept="image/*" :id="sign" :multiple="type==='moreone'" style="display:none">
      </label>
    </div>
  </div>
</template>

<script>
import utils from '@/utils/utils'
import defualtImg from '@/assets/img/recognize/ph@3x.png'
export default {
  props: {
    image: {
      type: String,
      default: defualtImg
    },
    title: {
      type: String,
      default: ''
    },
    message: {
      type: String,
      default: ''
    },
    sign: String,
    type: String
  },
  data () {
    return {
      defaultimg: defualtImg
    }
  },
  methods: {
    changeFile (e) {
      utils.uploadPic(e.target.files, (data) => {
        this.$emit('submit-pic', data, this.sign)
      })
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';

  .img-block {
    background: @whiteColor;
    overflow: hidden;
    position: relative;
    height: 1.2rem;
    .img-left {
      margin-right: 2rem;
      .hd {
        font-size: .32rem;
        color: @defaultColor
      }
      .msg {
        margin-top: .2rem;
        color: @lightColor;
        font-size: .28rem;
        line-height: .35rem;
      }
    }
    .img-right {
      position: absolute;
      top:0;
      right:0;
      img {
        width: 2rem;
        height: 1.2rem;
      }
    }
  }
</style>



