<template lang="html">
  <div class="">
    <default-head header="" path="/personcard"></default-head>
    <div id="wrapper" class="">
      <div class="re-form-box">
        <flm-form-input label="" width="2rem" v-model="userInfo.realName"
        data-vv-name="" v-validate="'required'" name="realName"
        placeholder=""></flm-form-input>
      </div>
      <div class="re-form-box">
        <flm-form-input label="" width="2rem" v-model="userInfo.idCard"
         data-vv-name="" v-validate="'required'" name="idCardNo" @keyup="checkNumLetter"
         placeholder=""></flm-form-input>
      </div>
      <div class="pic-box f-re-martop">
        <img-block @submit-pic="getImages" sign="idCardPic"
          :image="userInfo.idCardPic" title=""
          data-vv-name="" v-validate="'required'" data-vv-value-path="image"
          message=", , "
        ></img-block>
      </div>
      <div class="pic-box">
        <img-block @submit-pic="getImages" sign="idCardBackPic"
          :image="userInfo.idCardBackPic" title=""
          data-vv-name="" v-validate="'required'" data-vv-value-path="image"
          message=", "
        ></img-block>
      </div>
      <div class="pic-box">
        <img-block @submit-pic="getImages" sign="idCardHandPic"
          :image="userInfo.idCardHandPic" title=""
          data-vv-name="" v-validate="'required'" data-vv-value-path="image"
          message=", "
        ></img-block>
      </div>
      <div v-if="userInfo.status === 2 && userInfo.auditRemark"
      class="recognize-remark">: {{userInfo.auditRemark}}</div>
    </div>
    <foot-btn @click.native="submitRecognize"></foot-btn>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import imgBlock from '@/components/recognize/imgBlock'
import footBtn from '@/components/common/footBtn'

export default {
  components: {defaultHead, imgBlock, footBtn},
  data () {
    return {
      userInfo: {
        userId: 0,
        realName: '',
        idCard: '',
        idCardPic: '',
        idCardBackPic: '',
        idCardHandPic: '',
        status: 0,
        auditRemark: ''
      }
    }
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  mounted () {
    this.initPage()
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    checkNumLetter (e) {
      var val = e.target.value
      this.userInfo.idCard = val.replace(/[^0-9a-zA-Z]/g, '')
    },
    initPage () {
      if (!this.isLogin()) return
      this.ajaxGetUserInfo({userId: this.userId})
    },
    submitRecognize () {
      if (!this.isLogin()) return
      // 
      if (this.userInfo.status === 3) {
        minutils.MessageBox({message: ', '})
        return
      }
      if (this.userInfo.status === 1) {
        minutils.MessageBox({message: ', '})
        return
      }

      this.$validator.validateAll().then(() => {
        this.ajaxSaveUserRecognize(this.userInfo)
      }).catch((e) => {
        minutils.Toast({ message: this.errors.all()[0], position: 'top' })
      })
    },
    getImages (data, sign) {
      this.ajaxUploadPic({fileName: data[0].fileName, fileBase64: data[0].base64Img}, sign)
    },
    ajaxGetUserInfo (data) {
      minutils.IndicatorOpen('...')
      api.getUserInfo(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess && result.data) {
          Object.keys(this.userInfo).forEach(k => {
            if (result.data[k]) {
              this.userInfo[k] = result.data[k]
            }
          })
        }
      })
    },
    ajaxSaveUserRecognize (data) {
      minutils.IndicatorOpen('...')
      api.updatePersonInfoAuthentication(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          minutils.MessageBox({message: ''})
        }
      })
    },
    ajaxUploadPic (data, sign) {
      api.uploadPic(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess) {
          this.userInfo[sign] = result.data
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/recognize/index.less';
</style>



